@use 'utils/mixins'as *;


.header__ham {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    display: flex;
    flex-direction: column;
    gap: 0.8rem;
    cursor: pointer;

    &__line--1,
    &__line--2,
    &__line--3 {
        height: 0.5rem;
        width: 5rem;
        background: #fff;
        border-radius: 3px;
        box-shadow: 0 2px 10px 0 rgba(0, 0, 0, 0.3);
    }

    &__line--1 {
        animation: top-back 0.4s ease-in-out forwards;
    }

    &__line--2 {
        transform: scale(1);
        transition: 0.4s ease-in-out;
    }

    &__line--3 {
        animation: bot-back 0.4s ease-in-out forwards;
    }
}

.header__ham.act {
    .header__ham__line--1 {
        animation: top-forw 0.4s ease-in-out forwards;
    }

    .header__ham__line--2 {
        transform: scale(0);
        transition: 0.4s ease-in-out;
    }

    .header__ham__line--3 {
        animation: bot-forw 0.4s ease-in-out forwards;
    }
}

@keyframes top-forw {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(1.3rem);
    }

    100% {
        transform: translateY(1.3rem) rotate(45deg);
    }
}

@keyframes bot-forw {
    0% {
        transform: translateY(0);
    }

    50% {
        transform: translateY(-1.3rem);
    }

    100% {
        transform: translateY(-1.3rem) rotate(135deg);
    }
}

@keyframes top-back {
    0% {
        transform: translateY(1.3rem) rotate(45deg);
    }

    50% {
        transform: translateY(1.3rem);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes bot-back {
    0% {
        transform: translateY(-1.3rem) rotate(135deg);
    }

    50% {
        transform: translateY(-1.3rem);
    }

    100% {
        transform: translateY(0);
    }
}